:root{
    --Deep-green: #164B2E;
    --Light-green: #2D995F; 
    --Gray:#f2f3f3;
    --White: #ffffff;
    --Black: #343A40;
    /* --Background:rgba(172, 231, 199, 0.05); */
  }
  
  .mainbody {
    max-width: 725px;
    margin: 0 auto;
    padding: 30px;
    /* border: 1px solid #e0e0e0; */
    border-radius: 12px;
    background-color: #ffffff;
    color: #333333;
    box-shadow: 0 2px 4px -1px #101828;
  }

  .maincontainer {
    display: flex;
    /* gap: 15px; */
    justify-content: space-between;
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* background-color: red; */
  }

  .lftcontainer {
      background: linear-gradient(180deg, #21B55A, #0E4F27);
      height: 100vh;
      width: 40%;
      padding: 20% 2% 20% 2%;  
    }

    .logoimage{
        width: 100%;
      }

      .loginclk{
        color: #21B55A;
        cursor: pointer;        
      }

      .loginclk:hover{
        color: #21B55A;
        cursor: pointer;
        font-size: 15px; 
        font-weight: 700;
      }

      .slideimage{
        width: 120px;
        height: 10px;
        object-fit: contain;

      }

      .logohead {
        padding: 40px;
      }

      .slidehead4 {
        display: flex;
        justify-content: left;
        margin-top: 10px;
        padding: 0 0 0 36.5px;
      }
      
      .indicator {
        width: 30px;
        height: 4px;
        margin: 0 5px;
        /* border-radius: 50%; */
        background-color: darkgray;
        cursor: pointer;
        transition: background-color 0.3s, transform 0.3s;
      }
      
      .activeIndicator {
        background-color: white;
        transform: scale(1.2);
      }


      .slidehead {
        padding: 0 0 0 40px;
      }
 
      .maintext {
        width:65%
        /* padding: 6px 6px; */
        /* padding: 25px 170px 0 170px; */
        /* width: 100%;
        padding: 25px 170px 0 170px; */
      }
  
      .maintext h6 {
        color: #667185;
        font-size: 18px;
        font-weight: 400;
        text-align: left;
        margin: 5px 0 20px 0
      }

      .maintext h1 {
        color: #101928;
        font-size: 28px;
        font-weight: 700;
        text-align: left;
       
      }

      .maintext p {
        color: #667185;
        font-size: 13px;
        font-weight: 400px;
        text-align: center;
        margin: 0;
      }

      .inputField {
        width: 100%;
        height: 43px;
        margin-bottom: 10px;
        /* margin: 0 0 15px 0; */
      }
      .inputLabel{

font-size: 14px;
font-weight: 500;
line-height: 20.3px;
text-align: left;
text-decoration-skip-ink: none;

      }

      .inputField:focus {
        border-color: green;
        outline: none;
        box-shadow: 0 0 5px rgba(33, 181, 90, 0.5);
      }

      .errorCt {
        width: 38.5%;
        display: flex;
        justify-content: space-between;
        padding: 5px 14px 5px 14px;
        background-color: #ffffff; 
        border-radius: 7px;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px rgba(255, 255, 255, 1); /* Neumorphic shadow effect */
        border-top: 3px solid #21B55A; /* Top border only */
        border-right: 3px solid #21B55A; /* Right border only */
        border-bottom: none; /* Remove bottom border */
        border-left: none; /* Remove left border */
        z-index: 999;
        position: fixed;
        margin: -20px 0 0 0;
        /* display: none; */
        }


      .errorCt p {
        color: red;
      }

      .errorCt::before {
        content: '';
        position: absolute;
        top: -10px; /* Position above the container */
        right: 10px; /* Adjust to align with the top-right edge */
        width: 0;
        height: 0;
        border-left: 15px solid transparent; /* Left side of triangle */
        border-right: 10px solid transparent; /* Right side of triangle */
        border-bottom: 10px solid green; /* Bottom of triangle (visible part) */
        /* background-color: green; */
        color: green;
        }

      .btngreen {
        margin: 10px 0 15px 0;
        width: -webkit-fill-available;
        background-color: #21B55A;
        font-size: 16px;
        height: 40px;
        border: none;
        border-radius: 20px;
      }
  
      .textdown {
        padding: 0 0 0 40px;
        margin: 300px 0 0 0;
      }

      .textdownH {
        font-size: 24px;
        font-weight: 800px;
        color: white;
      }

      .textdownP {
        font-size: 13px;
        font-weight: 700px;
        color: white;
      }

    .rgtcontainer {
      /* background-image: url('../../assets/promix/signup.svg'); */
      /* background-size: cover; */
      background-color: var(--White);
      /* background-position:center; */
      /* background-repeat: no-repeat; */
      /* justify-content: right;   */
      height: 100vh;
      width: 60%;
      display: flex;
      justify-content: center; 
      align-items: center; 
    }
  
    .btmTxt p{
    text-align: center;
    font-size: 16px;
    color: #323232;
    margin-top: -10px;  
    }

    .thecntnt{
      display: flex;
      flex-direction: column;
      color: var(--White);
      gap: 5px;
      height: 500px;
    }
    .thecntnt form{
      display: flex;
      flex-direction: column;
      color: var(--White);
      gap: 5px;
      width: 100%;
      overflow-y: auto;
    }
    .thecntnt .formGroup{
      margin-right: 10px;
    }
    .thecntnt form::-webkit-scrollbar{
      width: 8px;
    }
    .thecntnt form::-webkit-scrollbar-thumb{
      background: rgb(223, 222, 222);
      border-radius: 4px;
    }
    .thecntnt form::-webkit-scrollbar-thumb:hover{
      background: var(--Light-green);
      border-radius: 4px;
    }
    .head{
      text-align: center;
    }
    .thecntnt p{
      color: var(--White);
    }
  
    .SignUptxt {
      font-family: 'inter';
      font-weight: 800;
      font-size: 40px;
      margin-bottom: 10px;
    }
  
    .SignUptxtsub {
      font-family: 'inter';
      font-weight: 400;
      font-size: 20px;
      display: flex;
      justify-content: center;
      text-align: center;
      padding-bottom: 15px;
    }
  
    .snupbtn {
      /* background-color: #2D995F; */
      color: white;
      width: 100%;
      margin-top: 32px;
      font-size: 14px;
      font-weight: 400;
      font-family: 'inter';
      padding: 12px 100px;
      /* margin-right: 10px; */
    }
  
    .lgin {
      font-weight: 400;
      font-size: 14px;
      text-align: center;
    }
  
    .lgin span {
      color: #2D995F;
    }

