        height: auto;
      }
    
      /* Left Container */
      .lftcontainer {
        width: 100%;
        height: auto;
        padding: 20px;
      }

      .lftcontainer{
        display: none;
      }
    
      .logohead {
        padding: 20px 10px;
      }
    
      .slidehead {
        padding: 10px 0;
      }
    
      .maintext {
        padding: 20px 10px;
        text-align: center;
        width: 100%;
      }
    
      .maintext h6,
      .maintext h1 {
        text-align: left;
      }
    
      .btngreen {
        width: 90%;
        font-size: 14px;
        height: 40px;
      }
    
      .textdown {
        margin: 20px 0 0 0;
        padding: 0;
        text-align: center;
      }
    
      .textdownH,
      .textdownP {
        text-align: center;
      }

      .inputLabel{

