        padding: 20px;
      }
    
      .thecntnt {
        height: auto;
        gap: 10px;
      }
    
      .thecntnt form {
        gap: 15px;
      }
    
      .thecntnt .formGroup {
        margin-right: 0;
      }
    
      .SignUptxt {
        font-size: 30px;
      }

      .slidehead4 {
        display: flex;
        justify-content: left;
        margin-top: 10px;
        padding: 0 0 0 36.5px;
        display: none;
      }
    
      .SignUptxtsub {
        font-size: 16px;
        padding-bottom: 10px;
      }
    
      .snupbtn {
        padding: 12px;
