        width: 100%;
      }
    
      .lgin {
        font-size: 12px;
      }
    
      .lgin span {
        font-size: 12px;
      }
    
      .customCarousel {
        margin-top: 0;
        display: none;
      }
    
      .carousel-indicators {
        justify-content: center !important;
      }

      .maintext h1 {
        font-size: 20px;
      }
    
      .maintext p {
        font-size: 14px;
      }
    
      .SignUptxt {
        font-size: 24px;
      }
    
      .SignUptxtsub {
        font-size: 14px;
      }
    
      .btngreen {
        font-size: 12px;
        height: 38px;
      }
    
      .textdownH {
        font-size: 18px;
      }
    
      .textdownP {
        font-size: 12px;
      }
    
      .snupbtn {
        font-size: 12px;
        padding: 10px;
      }

    }





