:root{
  --Deep-green: #164B2E;
  --Light-green: #2D995F; 
  --Gray:#f2f3f3;
  --White: #ffffff;
  --Black: #343A40;
  /* --Background:rgba(172, 231, 199, 0.05); */
}

.mainbody {
  max-width: 725px;
  margin: 0 auto;
  padding: 30px;
  /* border: 1px solid #e0e0e0; */
  border-radius: 12px;
  background-color: #ffffff;
  color: #333333;
  box-shadow: 0 2px 4px -1px #101828;
}

.maincontainer {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 100vh; 
  margin-top: 3rem;
}

.lftcontainer {
    background: linear-gradient(180deg, #21B55A, #0E4F27);
    height: 100vh;
    width: 35%;  
  }

  .logoimage{
      width: 183px;
      height: 35px;
      object-fit: contain;
    }

    .slideimage{
      width: 120px;
      height: 10px;
      object-fit: contain;

    }

    .slidehead4 {
      display: flex;
      justify-content: left;
      margin-top: 10px;
      padding: 0 0 0 36.5px;
    }
    
    .indicator {
      width: 30px;
      height: 4px;
      margin: 0 5px;
      /* border-radius: 50%; */
      background-color: darkgray;
      cursor: pointer;
      transition: background-color 0.3s, transform 0.3s;
    }
    
    .activeIndicator {
      background-color: white;
      transform: scale(1.2);
    }


    .successimage{
      width: 100px;
      height: 100px;
      object-fit: contain;
      margin: 0 0 20px 0
    }

    .logohead {
      padding: 40px;
    }

    .slidehead {
      padding: 0 0 0 40px;
    }

    .otpGroup {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 20px 0 25px 0
    }
    
    .otpInputs {
      display: flex;
      justify-content: center;
      gap: 20px;
    }
    
    .otpInput {
      width: 77px;
      height: 69px;
      text-align: center;
      font-size: 30px;
      font-weight: 900px;
      border: 1px solid #e4e4e4;
    }

    .otpInput:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(33, 181, 90, 0.5);
    }

    .maintext {
      width: 500px;
      padding: 20px;
      border: 1px solid #392DCA21;
      /* padding: 120px 200px 120px 200px; */
      /* justify-content: center;
      align-content: center; */
      text-align: center;
      border-radius: 24px;

    }

    .maintext h6 {
      color: #667185;
      font-size: 15px;
      font-weight: 400px;
      line-height: 25px;
      /* text-align: left; */
    }

    .maintext h1 {
      color: black;
      font-size: 25px;
      font-weight: 750;
      /* text-align: left; */
    }

    .maintext p {
      color: #667185;
      font-size: 15px;
      font-weight: 400px;
    }

    .btngreen {
      margin: 20px 0 15px 0;
      width: -webkit-fill-available;
      background-color: #21B55A;
      font-size: 16px;
      height: 40px;
      border: none;
    }

    .textdown {
      padding: 0 0 0 40px;
      margin: 300px 0 0 0;
    }

    .textdownH {
      font-size: 24px;
      font-weight: 800px;
      color: white;
    }

    .textdownP {
      font-size: 13px;
      font-weight: 700px;
      color: white;
    }

  .rgtcontainer {
    /* background-image: url('../../assets/promix/signup.svg'); */
    /* background-size: cover; */
    background-color: var(--White);
    /* background-position:center; */
    /* background-repeat: no-repeat; */
    /* justify-content: right;   */
    height: 100vh;
    /* width: 65%; */
  }

  

  .thecntnt{
    display: flex;
    flex-direction: column;
    color: var(--White);
    gap: 5px;
    height: 500px;
  }
  .thecntnt form{
    display: flex;
    flex-direction: column;
    color: var(--White);
    gap: 5px;
    width: 100%;
    overflow-y: auto;
  }
  .thecntnt .formGroup{
    margin-right: 10px;
  }
  .thecntnt form::-webkit-scrollbar{
    width: 8px;
  }
  .thecntnt form::-webkit-scrollbar-thumb{
    background: rgb(223, 222, 222);
    border-radius: 4px;
  }
  .thecntnt form::-webkit-scrollbar-thumb:hover{
    background: var(--Light-green);
    border-radius: 4px;
  }
  .head{
    text-align: center;
  }
  .thecntnt p{
    color: var(--White);
  }

  .SignUptxt {
    font-family: 'inter';
    font-weight: 800;
    font-size: 40px;
    margin-bottom: 10px;
  }

  .SignUptxtsub {
    font-family: 'inter';
    font-weight: 400;
    font-size: 20px;
    display: flex;
    justify-content: center;
    text-align: center;
    padding-bottom: 15px;
  }

  .snupbtn {
    /* background-color: #2D995F; */
    color: white;
    width: 100%;
    margin-top: 32px;
    font-size: 14px;
    font-weight: 400;
    font-family: 'inter';
    padding: 12px 100px;
    /* margin-right: 10px; */
  }

  .lgin {
    font-weight: 400;
    font-size: 14px;
    text-align: center;
  }

  .lgin span {
    color: #2D995F;
  }


  @media screen and (max-width: 900px) {
    /* Main Body */
    .mainbody {
      max-width: 100%;
      padding: 20px;
      border-radius: 8px;
      box-shadow: none; /* Optional for a cleaner mobile look */
    }
  
    /* Main Container */
    .maincontainer {
      flex-direction: column;
      justify-content: flex-start;
      height: auto;
    }
  
    /* Left Container */
    .lftcontainer {
      width: 100%;
      height: auto;
      padding: 20px;
    }

    .lftcontainer{
      display: none;
    }
  
    .logohead {
      padding: 20px 10px;
    }
  
    .slidehead {
      padding: 10px 0;
    }
  
    .maintext {
      padding: 20px 10px;
      text-align: center;
    }
  
    .maintext h6,
    .maintext h1 {
      text-align: center;
    }
  
    .btngreen {
      width: 90%;
      font-size: 14px;
      height: 40px;
    }
  
    .textdown {
      margin: 20px 0 0 0;
      padding: 0;
      text-align: center;
    }
  
    .textdownH,
    .textdownP {
      text-align: center;
    }

    .inputLabel{

      font-size: 14px;
      font-weight: 500;
      line-height: 20.3px;
      text-align: left !important;
      text-decoration-skip-ink: none;
      width: 100%;
      
    }
  
    /* Right Container */
    .rgtcontainer {
      width: 100%;
      height: auto;
      padding: 20px;
    }
  
    .thecntnt {
      height: auto;
      gap: 10px;
    }
  
    .thecntnt form {
      gap: 15px;
    }
  
    .thecntnt .formGroup {
      margin-right: 0;
    }
  
    .SignUptxt {
      font-size: 30px;
    }

    .slidehead4 {
      display: flex;
      justify-content: left;
      margin-top: 10px;
      padding: 0 0 0 36.5px;
      display: none;
    }
  
    .SignUptxtsub {
      font-size: 16px;
      padding-bottom: 10px;
    }
  
    .snupbtn {
      padding: 12px;
      font-size: 14px;
      width: 100%;
    }
  
    .lgin {
      font-size: 12px;
    }
  
    .lgin span {
      font-size: 12px;
    }
  
    .customCarousel {
      margin-top: 0;
      display: none;
    }
  
    .carousel-indicators {
      justify-content: center !important;
    }

    .maintext h1 {
      font-size: 20px;
    }
  
    .maintext p {
      font-size: 14px;
    }
  
    .SignUptxt {
      font-size: 24px;
    }
  
    .SignUptxtsub {
      font-size: 14px;
    }
  
    .btngreen {
      font-size: 12px;
      height: 38px;
    }
  
    .textdownH {
      font-size: 18px;
    }
  
    .textdownP {
      font-size: 12px;
    }
  
    .snupbtn {
      font-size: 12px;
      padding: 10px;
    }

    .otpGroup {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 20px 0 25px 0
    }
    
    .otpInputs {
      display: flex;
      justify-content: center;
      gap: 20px;
    }
    
    .otpInput {
      width: 100%;
      height: 100%;
      text-align: center;
      font-size: 30px;
      font-weight: 700;
      border: 1px solid #e4e4e4;
    }

    .otpInput:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(33, 181, 90, 0.5);
    }

  }