  /* Accordion Header */
  .headAcc {
    font-size: 1.125rem;
    font-weight: 600;
    color: #374151;
    /* padding: 1rem 1.5rem; */
    background-color: #ffffff;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    transition: all 0.3s ease;
  }
  
  

  .titleAccdP {
    font-size: 1rem;
    color: #4b5563;
    line-height: 1.6;
    padding: 1.5rem;
    background-color: #ffffff;
    border: 1px solid #e5e7eb;
    border-top: none;
    border-radius: 0 0 8px 8px;
  }
  
  /* Links in Accordion Body */
  .titleAccdPB {
    color: #2563eb;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.2s ease;
  }
  
  .titleAccdPB:hover {
    color: #1d4ed8;
    text-decoration: underline;
  }
  
  /* List Styling */
  .titleAccdP ul {
    list-style-type: disc;
    margin-left: 1.5rem;
    margin-top: 0.5rem;
  }
  
  .titleAccdP li {
    margin-bottom: 0.5rem;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .faq {
      padding: 2rem 1rem;
    }
  
    .freqTitle {
      font-size: 2rem;
    }
  
    .headAcc {
      font-size: 1rem;
      padding: 0.75rem 1rem;
    }
  
    .titleAccdP {
      font-size: 0.95rem;
      padding: 1rem;
    }
  }
  
  /* Additional Styling for Paragraph Above Accordion */
  .faq > p {
    text-align: center;
